import qiniuUploader from '../../../common/qiniuUploader.js'
import uniSteps from '@/components/uni-steps.vue'
var photoArray = []
export default {
       
	components: {

		uniSteps
	},
	data() {
		return {
		
			opcity: 0.5,
			photo1:'../../../static/images/pagesA/my/id-car1.png',
			photo2:'../../../static/images/pagesA/my/id-car2.png',
			name:'',
			carnum:'',
			token: '',
			photoArray: []
			
		}
	},
	onLoad(options) {
		this.name = options.name

	},
	onReady() {

	},
	methods: {
		//上传照片正面
		uploadphoto1(){
			//获取七牛上传token
			uni.request({
				url: this.baseUrl + "/qiniu-upload",
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					if (res.data.status == 1) {
						//获取七牛返回的token
						this.token = res.data.data.token;
						console.log(this.token)
						//选择头像图片获取临时地址
						let _self = this
						uni.chooseImage({
							count: 1,
							sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
							sourceType: ['album', 'camera'], //从相册选择，或打开照相机
							success: function(res) {
								console.log('res1', res);
								
								//图片临时地址
								qiniuUploader.upload(res.tempFilePaths[0], (success) => {
									console.log('===========' + success.imageURL);
									_self.photoArray.push(success.imageURL)
									 _self.photo1 = 'http://ceshi.8kpay.com/'+success.imageURL
									
								}, (error) => {
									console.log(error);
								}, {
									region: 'SCN',
									key: new Date().getTime().toString(),
									uptoken: _self.token
								}, (res) => {
									console.log('上传进度', res.progress);
								})
								
							},
							error: (e) => {
								console.log(e);
							}
						});
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		//上传照片反面
		uploadphoto2(){
			//获取七牛上传token
			uni.request({
				url: this.baseUrl + "/qiniu-upload",
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					if (res.data.status == 1) {
						//获取七牛返回的token
						this.token = res.data.data.token
						console.log(this.token)
						//选择头像图片获取临时地址
						let _self = this
						uni.chooseImage({
							count: 1,
							sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
							sourceType: ['album', 'camera'], //从相册选择，或打开照相机
							success: function(res) {
								console.log('res1', res);
								
								//图片临时地址
								qiniuUploader.upload(res.tempFilePaths[0], (success) => {
								console.log('===========' + success.imageURL);
								_self.photoArray.push(success.imageURL)
								 _self.photo2 = 'http://ceshi.8kpay.com/'+success.imageURL
								
									
								}, (error) => {
									console.log(error);
								}, {
									region: 'SCN',
									key: new Date().getTime().toString(),
									uptoken: _self.token
								}, (res) => {
									console.log('上传进度', res.progress);
								})
								
								
							},
							error: (e) => {
								console.log(e);
							}
						});
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		
		comfirme() {
			var _self = this		
			uni.request({
				url: this.baseUrl + "/auth-member",
				method:'POST',
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				data:{
					IdCard:uni.getStorageSync('indentifyCardNum'),
					Name:_self.name,
					// IdCardImg:JSON.stringify(photoArray1),
					IdCardImg:JSON.stringify(_self.photoArray)
				},
				success: (res) => {
					console.log(res.data)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else if (res.data.status == 1) {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
						uni.navigateTo({
							url:'./examine'
						})
						
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})

		},
	}
}
